@font-face {
  font-family: Fornite Font;
  src: url('../components/fonts/fortnite.otf');
}

.nav {
  font-family: Fornite Font;
  letter-spacing: .1rem;
  background-color: #111827;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 50px;
}

.nav-links {
  display: flex;
  align-items: center;
  gap: 30px;
  list-style: none;
}

.shop-link {
  text-decoration: none;
  color: #f9fafb;
  padding: 10px;
  font-size: 3rem;
  transition: background-color 0.25s;
}

.text-link {
  text-decoration: none;
  color: #f9fafb;
  font-size: 2rem;
  transition: background-color 0.25s;
}

.text-link:hover:not(:last-child) , .shop-link:hover{
  background-color: #ff0000;
}

.icon-container {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #333;
  width: 50px;
  height: 50px;
  border-radius: 50%;
}

.item-counter {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: -10px;
  right: -10px;
  background-color: red;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  font-size: 1.5rem;
}

@media (max-width: 600px) {
  .nav {
    flex-direction: column;
  }
}